"use client";

import { useState } from "react";
import Navigation from "@/components/navigation";
import Footer from "@/components/footer";
import CityComparisonSelector from "@/components/city-comparison-selector";
import WeatherCard from "@/components/weather-card";
import type { City, WeatherData } from "@/types";
import { Card, CardContent } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Skeleton } from "@/components/ui/skeleton";

export default function ComparePage() {
  const [city1Data, setCity1Data] = useState<{ city: City; weather: WeatherData | null }>({
    city: null,
    weather: null,
  });
  const [city2Data, setCity2Data] = useState<{ city: City; weather: WeatherData | null }>({
    city: null,
    weather: null,
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleCompare = async (city1: City, city2: City) => {
    setIsLoading(true);
    setError(null);

    try {
      // –ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è –ø–µ—Ä–≤–æ–≥–æ –≥–æ—Ä–æ–¥–∞
      const weatherResponse1 = await fetch(`/api/weather/current/${city1.id}`);
      if (!weatherResponse1.ok) {
        throw new Error(`–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –¥–ª—è –≥–æ—Ä–æ–¥–∞ ${city1.name}`);
      }
      const weatherData1 = await weatherResponse1.json();

      // –ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è –≤—Ç–æ—Ä–æ–≥–æ –≥–æ—Ä–æ–¥–∞
      const weatherResponse2 = await fetch(`/api/weather/current/${city2.id}`);
      if (!weatherResponse2.ok) {
        throw new Error(`–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –¥–ª—è –≥–æ—Ä–æ–¥–∞ ${city2.name}`);
      }
      const weatherData2 = await weatherResponse2.json();

      // –û–±–Ω–æ–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ
      setCity1Data({ city: city1, weather: weatherData1 });
      setCity2Data({ city: city2, weather: weatherData2 });
    } catch (err) {
      console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö:", err);
      setError(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö: ${(err as Error).message}`);
    } finally {
      setIsLoading(false);
    }
  };

  // –†–∞—Å—á–µ—Ç —Ä–∞–∑–Ω–∏—Ü—ã —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä
  const calculateTempDifference = () => {
    if (!city1Data.weather || !city2Data.weather) return null;

    const temp1 = city1Data.weather.temperature.air.C;
    const temp2 = city2Data.weather.temperature.air.C;
    return temp1 - temp2;
  };

  // –†–∞—Å—á–µ—Ç —Ä–∞–∑–Ω–∏—Ü—ã –æ—â—É—â–∞–µ–º—ã—Ö —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä
  const calculateFeelsLikeDifference = () => {
    if (!city1Data.weather || !city2Data.weather) return null;

    const feelsLike1 = city1Data.weather.temperature.comfort.C;
    const feelsLike2 = city2Data.weather.temperature.comfort.C;
    return feelsLike1 - feelsLike2;
  };

  // –¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–Ω–∞—è —Ä–∞–∑–Ω–∏—Ü–∞ —Å –ø—Ä–∞–≤–∏–ª—å–Ω—ã–º —Ñ–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ–º
  const formatTempDifference = (diff: number) => {
    if (diff === 0) return "–æ–¥–∏–Ω–∞–∫–æ–≤–∞—è";
    return diff > 0 ? `–Ω–∞ ${Math.abs(diff)}¬∞C —Ç–µ–ø–ª–µ–µ` : `–Ω–∞ ${Math.abs(diff)}¬∞C —Ö–æ–ª–æ–¥–Ω–µ–µ`;
  };

  const tempDiff = calculateTempDifference();
  const feelsLikeDiff = calculateFeelsLikeDifference();

  return (
    <div className="flex min-h-screen flex-col">
      <Navigation />
      <main className="flex-1 py-8">
        <div className="container px-4 md:px-6">
          <h1 className="text-3xl font-bold mb-6">–°—Ä–∞–≤–Ω–µ–Ω–∏–µ –ø–æ–≥–æ–¥—ã –≤ –≥–æ—Ä–æ–¥–∞—Ö</h1>
          <p className="text-muted-foreground mb-8">
            –í—ã–±–µ—Ä–∏—Ç–µ –¥–≤–∞ –≥–æ—Ä–æ–¥–∞ –¥–ª—è —Å—Ä–∞–≤–Ω–µ–Ω–∏—è –ø–æ–≥–æ–¥–Ω—ã—Ö —É—Å–ª–æ–≤–∏–π –∏ —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–π –ø–æ –æ–¥–µ–∂–¥–µ
          </p>

          <CityComparisonSelector onCompare={handleCompare} />

          {isLoading && (
            <div className="space-y-4">
              <div className="grid gap-6 md:grid-cols-2">
                <Skeleton className="h-64" />
                <Skeleton className="h-64" />
              </div>
            </div>
          )}

          {error && (
            <Card>
              <CardContent className="p-6">
                <div className="text-center">
                  <h2 className="text-2xl font-bold text-red-600">–û—à–∏–±–∫–∞</h2>
                  <p className="mt-2">{error}</p>
                </div>
              </CardContent>
            </Card>
          )}

          {!isLoading && !error && city1Data.weather && city2Data.weather && (
            <>
              <div className="mb-6">
                <h2 className="text-2xl font-semibold mb-4">–†–µ–∑—É–ª—å—Ç–∞—Ç—ã —Å—Ä–∞–≤–Ω–µ–Ω–∏—è</h2>
                <Card className="mb-4">
                  <CardContent className="p-6">
                    <h3 className="text-xl font-medium mb-4">–û—Å–Ω–æ–≤–Ω—ã–µ —Ä–∞–∑–ª–∏—á–∏—è</h3>
                    <ul className="space-y-2">
                      <li>
                        <span className="font-medium">–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞: </span>
                        –í –≥–æ—Ä–æ–¥–µ {city1Data.city.name} {formatTempDifference(tempDiff)} —á–µ–º –≤
                        –≥–æ—Ä–æ–¥–µ {city2Data.city.name} ({city1Data.weather.temperature.air.C}¬∞C vs{" "}
                        {city2Data.weather.temperature.air.C}¬∞C)
                      </li>
                      <li>
                        <span className="font-medium">–û—â—É—â–∞–µ–º–∞—è —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞: </span>
                        –í –≥–æ—Ä–æ–¥–µ {city1Data.city.name} –æ—â—É—â–∞–µ—Ç—Å—è {formatTempDifference(feelsLikeDiff)} —á–µ–º –≤
                        –≥–æ—Ä–æ–¥–µ {city2Data.city.name} ({city1Data.weather.temperature.comfort.C}¬∞C vs{" "}
                        {city2Data.weather.temperature.comfort.C}¬∞C)
                      </li>
                      <li>
                        <span className="font-medium">–í–ª–∞–∂–Ω–æ—Å—Ç—å: </span>
                        {Math.abs(city1Data.weather.humidity - city2Data.weather.humidity) < 5
                          ? "–ü—Ä–∏–º–µ—Ä–Ω–æ –æ–¥–∏–Ω–∞–∫–æ–≤–∞—è –≤ –æ–±–æ–∏—Ö –≥–æ—Ä–æ–¥–∞—Ö"
                          : `–í –≥–æ—Ä–æ–¥–µ ${
                              city1Data.weather.humidity > city2Data.weather.humidity
                                ? city1Data.city.name
                                : city2Data.city.name
                            } –≤–ª–∞–∂–Ω–æ—Å—Ç—å –≤—ã—à–µ`}{" "}
                        ({city1Data.weather.humidity}% vs {city2Data.weather.humidity}%)
                      </li>
                      <li>
                        <span className="font-medium">–í–µ—Ç–µ—Ä: </span>
                        {Math.abs(city1Data.weather.wind.speed - city2Data.weather.wind.speed) < 2
                          ? "–°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞ –ø—Ä–∏–º–µ—Ä–Ω–æ –æ–¥–∏–Ω–∞–∫–æ–≤–∞—è"
                          : `–í –≥–æ—Ä–æ–¥–µ ${
                              city1Data.weather.wind.speed > city2Data.weather.wind.speed
                                ? city1Data.city.name
                                : city2Data.city.name
                            } –≤–µ—Ç–µ—Ä —Å–∏–ª—å–Ω–µ–µ`}{" "}
                        ({city1Data.weather.wind.speed} –º/—Å vs {city2Data.weather.wind.speed} –º/—Å)
                      </li>
                    </ul>
                  </CardContent>
                </Card>
              </div>

              <Tabs defaultValue="current">
                <TabsList className="w-full mb-6">
                  <TabsTrigger value="current" className="flex-1">
                    –¢–µ–∫—É—â–∞—è –ø–æ–≥–æ–¥–∞
                  </TabsTrigger>
                  <TabsTrigger value="recommendation" className="flex-1">
                    –†–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏
                  </TabsTrigger>
                </TabsList>

                <TabsContent value="current">
                  <div className="grid gap-6 md:grid-cols-2">
                    <Card>
                      <CardContent className="p-6">
                        <h3 className="text-xl font-semibold mb-4">{city1Data.city.name}</h3>
                        <WeatherCard weatherData={city1Data.weather} showDetails={true} />
                      </CardContent>
                    </Card>

                    <Card>
                      <CardContent className="p-6">
                        <h3 className="text-xl font-semibold mb-4">{city2Data.city.name}</h3>
                        <WeatherCard weatherData={city2Data.weather} showDetails={true} />
                      </CardContent>
                    </Card>
                  </div>
                </TabsContent>

                <TabsContent value="recommendation">
                  <div className="grid gap-6 md:grid-cols-2">
                    <Card>
                      <CardContent className="p-6">
                        <h3 className="text-xl font-semibold mb-4">–ü–æ–¥—Ö–æ–¥–∏—Ç –¥–ª—è {city1Data.city.name}</h3>
                        <div className="space-y-2">
                          <p>
                            <span className="font-medium">–í–µ—Ä—Ö–Ω—è—è –æ–¥–µ–∂–¥–∞: </span>
                            {city1Data.weather.temperature.air.C > 20
                              ? "–õ–µ–≥–∫–∞—è –æ–¥–µ–∂–¥–∞, —Ñ—É—Ç–±–æ–ª–∫–∞"
                              : city1Data.weather.temperature.air.C > 15
                              ? "–õ–µ–≥–∫–∞—è –∫—É—Ä—Ç–∫–∞ –∏–ª–∏ –∫–æ—Ñ—Ç–∞"
                              : city1Data.weather.temperature.air.C > 5
                              ? "–î–µ–º–∏—Å–µ–∑–æ–Ω–Ω–∞—è –∫—É—Ä—Ç–∫–∞"
                              : "–¢–µ–ø–ª–∞—è –∑–∏–º–Ω—è—è –∫—É—Ä—Ç–∫–∞"}
                          </p>
                          <p>
                            <span className="font-medium">–û–±—É–≤—å: </span>
                            {city1Data.weather.temperature.air.C > 20
                              ? "–õ–µ–≥–∫–∞—è –æ–±—É–≤—å, —Å–∞–Ω–¥–∞–ª–∏–∏"
                              : city1Data.weather.temperature.air.C > 10
                              ? "–ö—Ä–æ—Å—Å–æ–≤–∫–∏, –ª–µ–≥–∫–∏–µ –±–æ—Ç–∏–Ω–∫–∏"
                              : "–£—Ç–µ–ø–ª–µ–Ω–Ω–∞—è –æ–±—É–≤—å, –±–æ—Ç–∏–Ω–∫–∏"}
                          </p>
                          {city1Data.weather.temperature.air.C < 10 && (
                            <p>
                              <span className="font-medium">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ: </span>
                              –®–∞–ø–∫–∞, –ø–µ—Ä—á–∞—Ç–∫–∏, —à–∞—Ä—Ñ
                            </p>
                          )}
                          {city1Data.precipitation?.type === "rain" && (
                            <p className="text-blue-500">–ù–µ –∑–∞–±—É–¥—å—Ç–µ –∑–æ–Ω—Ç - –æ–∂–∏–¥–∞–µ—Ç—Å—è –¥–æ–∂–¥—å!</p>
                          )}
                        </div>
                      </CardContent>
                    </Card>

                    <Card>
                      <CardContent className="p-6">
                        <h3 className="text-xl font-semibold mb-4">–ü–æ–¥—Ö–æ–¥–∏—Ç –¥–ª—è {city2Data.city.name}</h3>
                        <div className="space-y-2">
                          <p>
                            <span className="font-medium">–í–µ—Ä—Ö–Ω—è—è –æ–¥–µ–∂–¥–∞: </span>
                            {city2Data.weather.temperature.air.C > 20
                              ? "–õ–µ–≥–∫–∞—è –æ–¥–µ–∂–¥–∞, —Ñ—É—Ç–±–æ–ª–∫–∞"
                              : city2Data.weather.temperature.air.C > 15
                              ? "–õ–µ–≥–∫–∞—è –∫—É—Ä—Ç–∫–∞ –∏–ª–∏ –∫–æ—Ñ—Ç–∞"
                              : city2Data.weather.temperature.air.C > 5
                              ? "–î–µ–º–∏—Å–µ–∑–æ–Ω–Ω–∞—è –∫—É—Ä—Ç–∫–∞"
                              : "–¢–µ–ø–ª–∞—è –∑–∏–º–Ω—è—è –∫—É—Ä—Ç–∫–∞"}
                          </p>
                          <p>
                            <span className="font-medium">–û–±—É–≤—å: </span>
                            {city2Data.weather.temperature.air.C > 20
                              ? "–õ–µ–≥–∫–∞—è –æ–±—É–≤—å, —Å–∞–Ω–¥–∞–ª–∏–∏"
                              : city2Data.weather.temperature.air.C > 10
                              ? "–ö—Ä–æ—Å—Å–æ–≤–∫–∏, –ª–µ–≥–∫–∏–µ –±–æ—Ç–∏–Ω–∫–∏"
                              : "–£—Ç–µ–ø–ª–µ–Ω–Ω–∞—è –æ–±—É–≤—å, –±–æ—Ç–∏–Ω–∫–∏"}
                          </p>
                          {city2Data.weather.temperature.air.C < 10 && (
                            <p>
                              <span className="font-medium">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ: </span>
                              –®–∞–ø–∫–∞, –ø–µ—Ä—á–∞—Ç–∫–∏, —à–∞—Ä—Ñ
                            </p>
                          )}
                          {city2Data.precipitation?.type === "rain" && (
                            <p className="text-blue-500">–ù–µ –∑–∞–±—É–¥—å—Ç–µ –∑–æ–Ω—Ç - –æ–∂–∏–¥–∞–µ—Ç—Å—è –¥–æ–∂–¥—å!</p>
                          )}
                        </div>
                      </CardContent>
                    </Card>
                  </div>
                </TabsContent>
              </Tabs>
            </>
          )}
        </div>
      </main>
      <Footer />
    </div>
  );
}
